import { axiosInstance } from "../../../../axios-Instance";
import { toast } from "react-toastify";
import { toastOptions } from "../../../../utils";
import { useContext } from "react";
import { AuthContext } from "../../../../context";
import { setLoginToken } from "../../../../storage";
import { useMutation } from "@tanstack/react-query";

const SERVER_ERROR = "There was an error contacting the server.";

async function userVerification(formData) {
	const data = await axiosInstance({
		url: `/Authentication/VerifyAccount?Token=${formData["token"]}&Email=${formData["email"]}`,
		method: "POST",
		headers: {
			"Content-Type": "application/json",
		},
	});

	return data?.data;
}

export function useVerification() {
	const authCtx = useContext(AuthContext);
	const { mutate, isError, error, isSuccess, reset } = useMutation({
		mutationFn: (formData) => userVerification(formData),
		onSuccess: (data) => {
			setLoginToken(data);
			authCtx.authenticate(data.token);
		},
		onError: (error) => {
			const err = error?.response?.data?.responseMessage
				? error?.response?.data?.responseMessage
				: SERVER_ERROR;
			toast.error(err, toastOptions);
		},
	});
	return { mutate, isError, error, isSuccess, reset };
}
