import React, { useState } from "react";
import { Button, FormGroup, Input } from "mtforms";
import { useNavigate } from "react-router-dom";
import { useIsMutating } from "@tanstack/react-query";
import { PrivatePaths } from "../../../routes/path";
import { errorAlert, successAlert } from "../../../utils";
import { useVerification } from "./hooks";

const VerifyAccount = () => {
	const navigate = useNavigate();
	const [formData, setFormData] = useState({});
	const [errors, setErrors] = useState({});
	const {
		mutate,
		isError,
		isSuccess,
		error: verificationError,
		reset,
	} = useVerification();
	const isLoading = useIsMutating();

	const handleChange = (name, value) => {
		setFormData({ ...formData, [name]: value });
	};

	const validationHandler = (name, error) => {
		setErrors({ ...errors, [name]: error });
	};

	const submitHandler = () => {
		mutate(formData);
	};

	if (isSuccess) {
		reset();
		successAlert("Verification Successful");
		navigate(PrivatePaths.DASHBOARD);
	}
	if (isError) {
		reset();
		errorAlert(verificationError);
	}

	return (
		<>
			<h1>Verify Account</h1>
			<FormGroup onSubmit={submitHandler}>
				<Input
					name="email"
					label="Email Address"
					value={formData["email"]}
					onChange={handleChange}
					type="email"
					validationHandler={validationHandler}
					error={errors.email}
					required={true}
					size="large"
				/>
				<Input
					name="token"
					label="Enter token"
					value={formData["token"]}
					onChange={handleChange}
					type="text"
					validationHandler={validationHandler}
					error={errors.token}
					required={true}
					size="large"
				/>
				<Button
					title="verify"
					size="large"
					className={isLoading ? "btn-disabled" : ""}
					loading={isLoading === 1}
					disabled={isLoading === 1}
				/>
			</FormGroup>
		</>
	);
};

export default VerifyAccount;
